import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import YDUI from 'vue-ydui'; /* 相当于import YDUI from 'vue-ydui/ydui.rem.js' */
import router from './router'
import axios from 'axios';
import VueAxios from 'vue-axios'
import cookie from './static/js/cookie'
import store from './store'

import VueAMap from 'vue-amap'  // 地图

const FastClick = require('fastclick')
document.addEventListener('DOMContentLoaded', function () {
    FastClick.attach(document.body);
}, false);


import 'element-ui/lib/theme-chalk/index.css'
import { Upload,Button,Card,Row,Col,Slider,Checkbox,CheckboxButton,
  CheckboxGroup, Input,Alert,Radio,RadioGroup,RadioButton,Pagination,
  InputNumber,} from 'element-ui'
Vue.use(Upload)
Vue.use(Button)
Vue.use(Card)
Vue.use(Row)
Vue.use(Col)
Vue.use(Slider)
Vue.use(Checkbox)
Vue.use(CheckboxButton)
Vue.use(CheckboxGroup)
Vue.use(Input)
Vue.use(InputNumber)
Vue.use(Alert)
Vue.use(Radio)
Vue.use(RadioGroup)
Vue.use(RadioButton)
Vue.use(Pagination)

window.document.addEventListener('touchstart', function (event) {
  /* Do Nothing */
}, false);

// 导入弹窗
import { Confirm, Toast, Notify, Loading } from 'vue-ydui/dist/lib.px/dialog';


import 'vue-ydui/dist/ydui.px.css'

Vue.use(VueAxios, axios)
Vue.use(YDUI)
Vue.use(VueRouter)
Vue.use(VueAMap) 
Vue.config.productionTip = false

Vue.prototype.$dialog = {
  confirm: Confirm,
  alert: Alert,
  toast: Toast,
  notify: Notify,
  loading: Loading,
}


VueAMap.initAMapApiLoader({
  key: 'cde776e2f02fba555a1ae42dcf3702d4',
  plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor','AMap.Geolocation','AMap.Geocoder'],
  uiVersion: '1.0' // 版本号
})



