import * as types from './mutation-types'

const mutations = {
  // [types.SET_SINGER](state, singer) {
  //   state.singer = singer
  // },
  [types.UPDATE_FILTER](state, filter) {
    state.filters = filter
  },

  [types.ADDFAVLIST](state,item){
    // console.info(item) // 测试值有没有被传过来

    console.log(item)

    let id = item.id
    // console.log(id)

    let record = state.favlist.find( n => n.id == id ) // find 函数找到一个 id == id 的对象
    if(record){

      console.log('记录已经存在，不加入')
        
    }else{
      console.log('执行加入')
      state.favlist.push(item)
    }

},

 

}

export default mutations


